.coming-soon-page {
  background-color: #f0f0f0;
}
.coming-soon-page .title {
  font-size: 26px;
  font-weight: 300;
  text-align: center;
  color: #333;
  line-height: 28px;
  margin: 25px 10px 55px 10px;
}
.coming-soon-page .place_balk_workers {
  width: 437px;
  height: 213px;
  position: relative;
  margin: 90px auto -10px;
}
.coming-soon-page .place_balk_workers .balk_workers {
  width: 437px;
  height: 213px;
  position: relative;
  background-image: url(/dva-boot-admin/static/media/workers.3bb77abc.png);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-color: transparent;
  background-size: 100%;
  z-index: 3;
}
.coming-soon-page .place_balk_workers .balk {
  width: 60px;
  height: 23px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-color: transparent;
  background-size: 100%;
  position: absolute;
  top: 90px;
  left: 178px;
}
.coming-soon-page .place_balk_workers .saw {
  width: 325px;
  height: 63px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-color: transparent;
  background-size: 100%;
  position: absolute;
  left: 53px;
  top: 68px;
  z-index: 2;
  -webkit-animation: 1s ease 0s normal none infinite saw;
          animation: 1s ease 0s normal none infinite saw;
  -webkit-animation: saw 1s infinite ease-in-out;
          animation: saw 1s infinite ease-in-out;
  -webkit-transform-origin: 85% 65%;
          transform-origin: 85% 65%;
}
.coming-soon-page #watch {
  color: #f6f7f9;
  text-align: center;
}
.coming-soon-page #watch .dash {
  display: inline-block;
  padding: 20px 30px;
  line-height: 72px;
  text-align: center;
  margin: 0 -3px;
}
.coming-soon-page #watch .dash:first-child {
  border-radius: 8px 0px 0px 8px;
  background-color: #0089e9;
}
.coming-soon-page #watch .dash:last-child {
  border-radius: 0px 8px 8px 0px;
  background-color: #00b6f9;
  padding-left: 28px;
  padding-right: 28px;
}
.coming-soon-page #watch .dash:nth-child(2) {
  background-color: #0097f6;
}
.coming-soon-page #watch .dash:nth-child(3) {
  background-color: #00a7f8;
}
.coming-soon-page #watch .dash .digit {
  display: block;
  width: 48px;
  font-size: 44px;
  line-height: 44px;
  height: 38px;
  float: left;
}
.coming-soon-page #watch .dash .digit .top,
.coming-soon-page #watch .dash .digit .bottom {
  font-weight: 300;
}
.coming-soon-page #watch .dash span.dash_title {
  font-size: 14px;
  line-height: 16px;
  padding-top: 6px;
  font-weight: 300;
  display: block;
  text-align: center;
  clear: both;
}
@-webkit-keyframes saw {
  0% {
    left: 53px;
  }
  50% {
    left: 72px;
  }
  100% {
    left: 53px;
  }
}
@keyframes saw {
  0% {
    left: 53px;
  }
  50% {
    left: 72px;
  }
  100% {
    left: 53px;
  }
}
